.header__menu {
  margin: 28px 8.44% 31.6px 0;
  display: none;
  width: 40px;
  cursor: pointer;
}

@media screen and (max-width: 550px) {
  .header__menu {
    display: block;
  }
}

.header__menu:hover {
  opacity: 0.6;
}

@media screen and (max-width: 320px) {
  .header__menu {
    width: 30px;
  }
}
